<template>
  <nav class="pnav">
    <div class="pnav__container">
      <router-link :to="{ name: 'portfolio' }" class="pnav__logo">
        <logo></logo>
      </router-link>
      <div class="pnav__links">
        <router-link :to="{ name: 'blog' }" class="button button--blue">Blog</router-link>
      </div>
    </div>
  </nav>
</template>
<script>
  import logo from '@/app/global/components/Logo'

  export default {
    name: 'navigation',
    components: {
      logo
    }
  }
</script>
<style lang="sass">
  @import '~sass/base/vars'

  .pnav
    display: flex
    justify-content: center
    width: 100%
    padding: 30px 50px
    background-color: white
    &__container
      display: flex
      align-items: center
      width: 100%
      max-width: 1600px
      height: inherit
    &__logo
      display: flex
      justify-content: flex-start
      align-items: center
      width: 50%
      height: 100%
    &__links
      display: flex
      align-items: center
      justify-content: flex-end
      width: 50%
      height: 100%
      margin: 0
      padding: 0
      list-style-type: none

  @media screen and (max-width: 1024px)
    .pnav
      padding: 30px 50px
  @media screen and (max-width: 768px)
    .pnav
      padding: 15px 35px
  @media screen and (max-width: 425px)
    .pnav
      padding: 15px 20px
</style>



